.cardCalculator{
  margin: auto 30%;
  padding: 20px;
  background: #283345;
  display: block;
  height: 500px;
  box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  border-radius: 10px;
  position: absolute; 
  top: 0;
  bottom: 0;
  left: 0; 
  right: 0;
}
@media only screen and (max-width: 920px) {
  .cardCalculator{
    margin: auto 10px;
  }
}
.cardCalculator h1{
  float: right;
  font-size: 40px;
  display: block;
  margin-right: 30px;
  font-weight: bold;
}
.container{
  height: 100vh;
}
.orangeColor{
  color: orange!important;
}
.cardFlex{
  display: flex;
}
.centerText{
  text-align: center;
  margin: 0;
}
.cardNumbering{
  display: flow-root;
  height: 100px;
}
.btnInput{
  padding: 10px;
  margin: 15px;
  width: 50%;
  font-size: 22px;
  background: #283345;
  color: #fff;
  border: none;
  padding-top: 10px;
  text-align: center;
  display: block;
  box-shadow: -3px -3px 9px 0px rgba(255, 255, 255, 0.03), 4px 5px 3px 0px rgba(27, 27, 27, 0.44);
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  -webkit-writing-mode: initial;
  -webkit-appearance: none;
  text-indent: unset;
}
.btnInput:active{
  box-shadow: none;
  outline: none !important;
  outline-offset: none !important;
}
.btnInput:hover{
  outline: none !important;
  outline-offset: none !important;
}
.btnInput:focus{
  outline: none !important;
  outline-offset: none !important;
}